.header-wrap {
	background: url(https://source.unsplash.com/nqSUnXINsp4/1600x900) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 47em;
}

.ui.container.main-content {
	padding-top: 7em;
}

.ui.small.icon.buttons {
	margin-bottom: 10px;
}

.ui.borderless.main.menu {
	border: none;
    margin: 0;
}

.ui.sticky.fixed.top {
	/*width: 2em;*/
	margin-top: 5em !important;
}

/*margin: 7rem 0 0 7rem !important;
	max-width: 60% !important;*/

.logotext {
	font-family: 'bagnard';
	/*font-family: 'young-serif';*/
}

.page-heading {
	font-size: 5em;
}

.tagline {
	/*font-family: 'et-book-italic';*/
	/*font-family: 'oldenburg';
	font-size: 1.5em;*/
	font-family: cmutypewritertextbold;
    font-size: 1.5em;
    font-kerning: normal;
    text-transform: uppercase;
}

