.workshop-list {
	padding: 0;
}

.workshop-heading {
	margin-top: 0;
}

.workshop-date {
	font-family: 'Oldenburg';
	float: right;
	margin-top: .6em;
}

.workshop-description {
	clear: both;
}

.inconsolata {
	font-family: 'Inconsolata';
	font-weight: 700;
}

.workshop-post-content {
	width: 25em;
}

.aside-tagline {
	font-family: cmutypewritertextbold;
    font-size: 1.7em;
    letter-spacing: 0.09em;
    margin-bottom: .5em;
    text-transform: uppercase;
}

ul {
	list-style-type: circle;
}

.workshop-featured-image {
	margin-bottom: 3em;
}